<!-- 布局 --> 
<template>
  <!--根部视图-->
  <div class="rootView">
    <div class="bottom-style">
      <span class="title-style"><strong>简书</strong></span>
      <ul class="ul-style">
        <li><a href="#"><span>发现</span></a></li>
        <li><a href="#"><span>关注</span></a></li>
        <li><a href="#"><span>消息</span></a></li>
        <li class="lisearch-style">
          <input type="search" placeholder="搜索">
          <img src="../../../static/image/search.png"></img>
        </li>
      </ul>
    </div>

    <div class="rightview-style">
      <div id="leftment-style"
           v-on:mouseenter="mouseentermenut"
           v-on:mouseleave="mouseleavemenut">
        <img src="../../../static/image/titlename.jpg" alt="">
        <div class="sanjiao-style"></div>
      </div>
      <span class="readContent-style">写文章</span>
    </div>

    <div class="menu-style"
         v-if="this.menutHidss"
         v-on:mouseenter="mouseentermenut"
         v-on:mouseleave="mouseleavemenut">
      <ul>
        <li>我的主页</li>
        <li>我的收藏</li>
        <li>我喜欢的文章</li>
        <li>设置</li>
        <li>退出</li>
      </ul>

    </div>

  </div>
   

</template>

  <!-- vue -->
<script>

  export default {
    data() {

      return {
        menutHidss: false

      }

    },
    methods: {

      mouseentermenut() {
        this.menutHidss = true
        let heardv = document.getElementById("leftment-style")
        heardv.style.backgroundColor = "#ececec"

      },

      mouseleavemenut() {
        this.menutHidss = false
        let heardv = document.getElementById("leftment-style")
        heardv.style.backgroundColor = "white"
      },

    }


  }


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 根部视图 */
  .rootView {
    width: 100%;
    min-width: 850px;
    max-width: 100%;
    height: 55px;
    background-color: white;
    border-bottom: 0.5px solid #dedede;
    text-align: left;
  }

  .bottom-style {
    text-align: left;
    display: inline-block;
    width: 600px;
  }

  .title-style {
    float: left;
    height: 55px;
    line-height: 55px;
    color: #e07061;
    font-size: 23px;
    margin-left: 20px;
  }

  .ul-style {
    display: inline-block;
    margin-left: 100px;
    height: 55px;
    line-height: 55px;
  }

  .ul-style li {
    display: inline-block;
    width: 70px;
    text-align: center;
  }

  .ul-style li a span {
    color: black;
    height: 55px;
    line-height: 55px;
    width: 70px;
    display: inline-block;

  }

  .lisearch-style {
    text-align: left;
    background-color: #ececec;
    border-radius: 20px;
    line-height: 40px;
    width: 160px !important;
    margin-left: 25px;
  }

  .ul-style li input {
    background-color: #ececec;
    line-height: 40px;
    width: 100px;
    font-size: 14px;
    clear: none;
  }

  .ul-style li img {
    width: 17px;
    height: 17px;
    position: relative;
    top: 5px;
  }

  .rightview-style {
    width: 200px;
    height: 55px;
    float: right;
    right: 30px;
    margin-right: 30px;
  }

  #leftment-style {
    display: inline-block;
    width: 85px;
    height: 54px;
    cursor: pointer;
  }

  #leftment-style img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-left: 10px;
    margin-top: 8px;

  }

  .readContent-style {
    position: relative;
    bottom: 15px;
    left: 6px;
    width: 100px;
    height: 40px;
    display: inline-block;
    background-color: #df6e5d;
    border-radius: 20px;
    line-height: 40px;
    text-align: center;
    color: white;
    font-size: 15px;
  }

  .sanjiao-style {
    width: 0;
    height: 0;
    border-width: 6px 6px 0;
    border-style: solid;
    border-color: #999999 transparent transparent; /*灰 透明 透明 */
    display: inline-block;
    position: relative;
    bottom: 15px;
  }

  .menu-style {
    clear: right;
    width: 150px;
    height: 200px;
    float: right;
    margin-right: 80px;
    background-color: white;
    box-shadow: 0px 0px 10px #bcbcbc;
  }

  .menu-style ul {
    width: 100%;
  }

  .menu-style ul li {
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-bottom: 0.5px solid #eeeeee;
    font-size: 14px;
  }

  .menu-style ul li:hover {
    background-color: #ececec;
    cursor: pointer;

  }


</style>



