<!-- 布局 --> 
<template>
  <!--根部视图-->
  <div class="rootView">
    <div v-for="temp in array" class="oneonly-style">
      <img v-bind:src=imageName alt="加载失败"><span>{{temp}}</span>
    </div>
    <span id="remen-style">更多热门专题 ></span>
  </div>
   

</template>

  <!-- vue -->
<script>

  export default {
    data() {

      return {
        array: [
          "连载小说",
          "简书电影",
          "散文",
          "时差党",
          "手帐圈",
          "社会热点",
          "谈写作",
        ],

        imageName: "http://upload.jianshu.io/collections/images/261938/man-hands-reading-boy-large.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/64/h/64"

      }

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 根部视图 */
  .rootView {
    width: 100%;
    text-align: left;
  }

  .oneonly-style {
    width: 110px;
    display: inline-block;
    height: 35px;
    border-radius: 3px;
    border: 0.5px solid #c6c6c6;
    margin-right: 45px;
    margin-top: 20px;
  }

  .oneonly-style img {
    width: 34px;
    height: 34px;

  }

  .oneonly-style span {
    line-height: 35px;
    height: 100%;
    text-align: center;
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    width: 75px;
  }

  #remen-style {
    width: 110px;
    height: 35px;
    display: inline-block;
    font-size: 14px;
    vertical-align: top;
    text-align: center;
    position: relative;
    top:28px;
    left: -10px;
  }

</style>



