<!-- 布局 --> 
<template>
  <!--根部视图-->
  <div class="root-style">
    <div class="leftImage-style">
      <el-carousel height="275px">
        <el-carousel-item v-for="item in imgarray" :key="item">

          <img v-bind:src=item style="width: 100%;height: 100%;">
        </el-carousel-item>
      </el-carousel>
    </div>
    <div id="rightimage-style">
      <ul class="rightul-style">
        <li v-for="temp in 10">
          <img
            src="http://upload.jianshu.io/admin_banners/web_images/3839/9d5fa7d00ad0a6f5f57a7101448e39983140e905.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540"
            alt="" style="width: 100%;height: 100%;">

        </li>
      </ul>

    </div>

  </div>
   

</template>

  <!-- vue -->
<script>

  export default {
    data() {

      return {
        imgarray: [
          "http://upload.jianshu.io/admin_banners/web_images/3839/9d5fa7d00ad0a6f5f57a7101448e39983140e905.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540",
          "http://upload.jianshu.io/admin_banners/web_images/3826/4ddbeb85a5ba968908e473f9eff84beba1208b34.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540",
          "http://upload.jianshu.io/admin_banners/web_images/3836/c10ac74806feebfd12bf47f5c8847a6dc56753df.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540"

        ]

      }

    },

    mounted() {

      var rightImage = document.getElementById("rightimage-style")

      setInterval(function () {

        console.log(rightImage.offsetTop);

        rightImage.style.top = rightImage.offsetTop - 275 -55 + "px"

        if (rightImage.offsetTop > 0) {

          rightImage.style.top = -rightImage.offsetHeight / 2 + "ps"

        } else if (rightImage.offsetTop < -rightImage.offsetHeight / 2) {

          rightImage.style.top = "0px"
        }

      }, 3000)


    }

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .root-style {
    text-align: left;
    font-size: 0;
  }

  .leftImage-style {
    display: inline-block;
    width: 630px;
    height: 275px;
    text-align: left;
    background-color: green;
  }

  #rightimage-style {
    display: inline-block;
    width: 315px;
    height: 2750px;
    background-color: red;
    position: relative;
    vertical-align: top;
    transition: 1s;
  }

  .rightul-style {
    height: 100%;
  }

  .rightul-style li {
    height: 275px;
    font-size: 16px;
  }

</style>




