<!-- 布局 --> 
<template>
  <!--根部视图-->
  <div class="rootView">
    <ul >
      <li v-for="temp in array">
        <img v-bind:src=temp alt="" >
      </li>
    </ul>
  <div class="erweima-style">
    <img src="../../../static/image/e6.png" alt="">
    <p><span>下载简书手机APP ></span><br>随时随地发现和创作内容<p/>
  </div>

  </div>
   

</template>

  <!-- vue -->
<script>

  export default {
    data() {

      return {
        array: ["../../../static/image/e1.png",
          "../../../static/image/e2.png",
          "../../../static/image/e3.png",
          "../../../static/image/e4.png",
          "../../../static/image/e5.png",]
      }

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 根部视图 */
  .rootView {
    text-align: right;
  }

  .rootView ul li img {
    width: 300px;
    height: 50px;
    margin-top: 10px;
  }

  .rootView ul {
   margin-top: 5px;

  }

  .erweima-style {
    width: 300px;
    height: 80px;
    margin-left: 10px;
    margin-top: 10px;
    border: 0.5px solid #d3d3d3;
    border-radius: 4px;
    text-align: left;
  }

  .erweima-style img {
    width: 60px;
    height: 60px;
    margin-left: 20px;
    margin-top: 10px;

  }

  .erweima-style p {
    display: inline-block;
    font-size: 13px;
    vertical-align: top;
    margin-top: 20px;
    color: #6b6b6b;
  }

  .erweima-style p span {
    font-size: 16px;
    color: black;
    padding-bottom: 4px;
    display: inline-block;

  }

</style>



